
.menu-wrapper {
  padding: none !important;
}
.menu-wrapper.type-sidemenu .menu-item .icon {
    margin-right: 20px;
    font-size: 24px;
    position: relative;
}
.menu-wrapper.type-sidemenu .menu-item .title {
    width: 100%;
    padding: 20px 30px;
    text-align: left;
    letter-spacing: 1px;
    text-transform: uppercase;
    transition: .1s;
}
.menu-wrapper.type-sidemenu .menu-item .sub-menu {
    background-color: rgba(128, 128, 128, 0.1);
}
.menu-wrapper.type-sidemenu .menu-item .sub-menu .title {
    padding: 15px 30px 15px 50px;
    font-size: .9rem;
}
.menu-wrapper.type-sidemenu .menu-item .sub-menu .title .icon {
      font-size: 20px;
}
.menu-wrapper.type-sidemenu .menu-item.active > .title {
    background-color: rgba(148, 66, 156, 0.6);
    color: white;
}
.menu-wrapper.type-sidemenu .menu-item .title:hover {
    background-color: #94429C;
    color: white;
}



